<template>
  <div class="header">
		城市选择
		<div class="iconfont header-back" @click="goToHome">&#xe624;</div>
	</div>
</template>

<script>
export default {
  name: 'CityHeader',
	props:{
		city:String
	},
  data () {
    return {
      
    }
  },
	methods:{
		goToHome (){
			this.$router.push('/')
		}
	}
	
}
</script>

<style scoped lang="scss">
	@import "~@/styles/varibles.scss";
.header{
	position: relative;
	overflow: hidden;
	height: $headerHeight;
	line-height: $headerHeight;
	text-align: center;
	color:#fff;
	background:$bgColor;
	font-size: .32rem;
	.header-back{
		position:absolute;
		top:0;
		left:0;
		width:.64rem;
		text-align: center;
		font-size: .4rem;
		color:#fff;
	}
}
</style>



